@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  .picker {
    position: relative;
  }
  .swatch {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    border: 3px solid #fff;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  .popover {
    position: absolute;
    bottom: 50px;
    left: -12px;
    border-radius: 9px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    background: #fff;
  }

  .picker__swatches {
    display: flex;
    padding: 10px;
    flex-wrap: wrap;
  }

  .picker__swatch {
    width: 22px;
    height: 22px;
    margin: 4px;
    border: none;
    padding: 0;
    border-radius: 20px;
    cursor: pointer;
    outline: none;
  }
}

body {
  background: linear-gradient(90deg, rgb(192 132 252) 0%, rgb(252 165 165) 100%); 
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#root {
  height: 100%;
}
